@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Sigmar+One&display=swap);
body{margin:0}.f-jcsb{display:flex;justify-content:space-between;align-items:center}.f-jcc{display:flex;justify-content:center;align-items:center}.fw{flex-wrap:wrap}.pow-icon{width:100px;height:100px}.animated-text{background:linear-gradient(to right, #b30be6, #770899);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-animation:0.7s animate-text infinite;animation:0.7s animate-text infinite}.error-text{color:#f73636 !important;-webkit-animation:0.3s shake-error-text infinite;animation:0.3s shake-error-text infinite}@-webkit-keyframes animate-text{0%{background-image:linear-gradient(to right, #b30be6, #770899)}25%{background-image:linear-gradient(to bottom, #b30be6, #770899)}75%{background-image:linear-gradient(to left, #b30be6, #770899)}100%{background-image:linear-gradient(to right, #b30be6, #770899)}}@keyframes animate-text{0%{background-image:linear-gradient(to right, #b30be6, #770899)}25%{background-image:linear-gradient(to bottom, #b30be6, #770899)}75%{background-image:linear-gradient(to left, #b30be6, #770899)}100%{background-image:linear-gradient(to right, #b30be6, #770899)}}@-webkit-keyframes shake-error-text{12.5%{transform:rotate(3.5deg)}25%{transform:rotate(0deg)}37.5%{transform:rotate(-3.5deg)}50%{transform:rotate(0deg)}62.5%{transform:rotate(3.5deg)}75%{transform:rotate(0deg)}87.5%{transform:rotate(-3.5deg)}100%{transform:rotate(0deg)}}@keyframes shake-error-text{12.5%{transform:rotate(3.5deg)}25%{transform:rotate(0deg)}37.5%{transform:rotate(-3.5deg)}50%{transform:rotate(0deg)}62.5%{transform:rotate(3.5deg)}75%{transform:rotate(0deg)}87.5%{transform:rotate(-3.5deg)}100%{transform:rotate(0deg)}}

.header{position:absolute;top:0;left:0;width:100%;height:60px}.header .brand{font-family:"Sigmar One";font-size:24px;letter-spacing:2px}.header .links a{width:30px;height:30px}.header .links a img{object-fit:contain}

.g-wrapper{position:relative;top:60px;left:0;width:90%;margin:0 5%;height:calc(100% - 120px)}.g-wrapper>input{width:0;height:0;padding:0;margin:0;border:0;outline:0;opacity:0}.g-wrapper .game-main-container .w-1{text-align:center;font-family:"Poppins";font-size:34px;color:#770899;word-break:break-all}.g-wrapper .game-main-container .w-1>span{opacity:0.3}.g-wrapper .game-main-container .w-2{text-align:center;font-family:"Poppins";font-size:18px;opacity:0.25;word-break:break-all}.g-wrapper .game-start-container>*{width:100%}.g-wrapper .game-start-container .instructions{font-family:"Poppins";font-size:16px;line-height:1.5;text-align:center;margin:20px 0;color:#000}.g-wrapper .game-start-container .instructions>div{margin-top:10px}.g-wrapper .game-start-container .start-button{text-align:center;border-radius:5px;width:200px;padding:15px;box-sizing:border-box;background-color:#b30be6;color:#fff;font-family:"Sigmar One";font-size:24px;letter-spacing:2px;border-bottom:3px solid #770899;cursor:pointer}.g-wrapper .game-start-container .start-button:active{background-color:#770899;border-bottom:3px solid #b30be6}.g-wrapper .game-over-container>*,.g-wrapper .game-paused-container>*{text-align:center;width:100%}.g-wrapper .game-over-container>h3,.g-wrapper .game-paused-container>h3{font-family:"Poppins";color:#770899}.g-wrapper .game-over-container>p,.g-wrapper .game-paused-container>p{font-family:"Poppins";font-size:16px}.g-wrapper .game-over-container>.restart-button,.g-wrapper .game-over-container>.resume-button,.g-wrapper .game-paused-container>.restart-button,.g-wrapper .game-paused-container>.resume-button{border-radius:5px;width:200px;padding:15px;box-sizing:border-box;background-color:#fff;color:#770899;font-family:"Poppins";font-size:24px;letter-spacing:2px;border:1px solid #770899;cursor:pointer}.g-wrapper .app-unexpected-error{font-family:"Poppins";font-size:16px;line-height:1.5;text-align:center;color:#000}.g-wrapper .app-unexpected-error>a{color:#770899;font-weight:bold;text-decoration:none}@media screen and (min-width: 768px){.g-wrapper{width:60%;margin:0 20%}}

.footer-wrapper{position:relative;top:60px;left:10%;width:80%;height:60px}.footer-wrapper .version{color:#770899;margin-right:10px;font-family:"Poppins";font-size:14px}.footer-wrapper .credits{font-size:12px}.footer-wrapper .credits>a{color:#770899;text-decoration:none}

.home-wrapper{position:absolute;top:0;left:10%;width:80%;height:100%}

